import axios from "axios";
import { baseUrls, normalLogin } from "./ApisUrls";

export async function getprams() {
  const token = localStorage.getItem("ACCESS_TOKEN");
  const ACCESS_TOKEN = {
    headers: {
      Authorization: `Bearer ${token}`,
    },
  };

  const api = baseUrls;
  const data = await axios.get(api, ACCESS_TOKEN);
  const redData = await data.data;
  console.log(redData);
  return redData;
}

export async function userLogin(username, password) {
  let body;
  let resData;
  const options = {
    headers: { "content-type": "application/json" },
  };
  if (["@"].some((a) => a !== username)) {
    body = {
      mobileNumber: username,
      password: password,
    };
  } else {
    body = {
      email: username,
      password: password,
    };
  }
  const data = JSON.stringify(body);
  await axios
    .post(normalLogin + "/api/auth-service/auth/sign-in", data, options)
    .then(function (response) {
      console.log("res" + response.data);
      resData = response.data;
    })
    .catch(function (error) {
      console.log(error);
    });
  return resData;
}

export async function listofAgent() {
  let listAgent;

  await axios
    .get("http://3.6.94.124:9001/api/student-service/student/listOfAgents")
    .then((resp) => {
      listAgent = resp.data;
      console.log(resp.data);
    });
  return listAgent;
}

export async function ListofUsersMapped(agentName) {
  let body;
  let agentData;
  const options = {
    headers: { "content-type": "application/json" },
  };
  body = {
    agentName: agentName,
    location: null,
    university: null,
  };

  const data = JSON.stringify(body);
  await axios
    .post(
      "http://3.6.94.124:9001/api/student-service/student/listOfUniversitiesMappedToAgent",
      data,
      options
    )
    .then(function (response) {
      console.log("res" + response.data);
      agentData = response.data;
    })
    .catch(function (error) {
      console.log(error);
    });

  return agentData;
}
