import React, { Component } from "react";
import SweetAlert from "sweetalert2-react";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { TabContent, TabPane, Nav, NavLink, NavItem } from "reactstrap";
import classnames from "classnames";

export default class Departments extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeTab: 1,
      isCardRemove: false,
      isCollapsed: false,
      show: false,
    };
  }
  render() {
    const { activeTab, isCardRemove, isCollapsed } = this.state;
    return (
      <>
        <div className="section-body">
          <div className="container-fluid">
            <div className="d-flex justify-content-between align-items-center">
              <div className="header-action">
                <h1 className="page-title">University </h1>
                {/* <ol className="breadcrumb page-breadcrumb">
                  <li className="breadcrumb-item">
                    <a href>Ericsson</a>
                  </li>
                  <li
                    className="breadcrumb-item active"
                    aria-current="page"
                  ></li>
                </ol> */}
              </div>
              <Nav tabs className="page-header-tab">
                <NavItem>
                  <NavLink
                    className={classnames({ active: activeTab === 1 })}
                    onClick={() => this.setState({ activeTab: 1 })}
                  >
                    View
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink
                    className={classnames({ active: activeTab === 2 })}
                    onClick={() => this.setState({ activeTab: 2 })}
                  >
                    Add university
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink
                    className={classnames({ active: activeTab === 3 })}
                    onClick={() => this.setState({ activeTab: 3 })}
                  >
                    Add Agents
                  </NavLink>
                </NavItem>
              </Nav>
            </div>
          </div>
        </div>
        <div className="section-body mt-4">
          <div className="container-fluid">
            <TabContent activeTab={activeTab}>
              <TabPane tabId={1} className={classnames(["fade show"])}>
                <div className="table-responsive">
                  <div className="table-responsive card">
                    <table className="table table-hover table-striped table-vcenter text-nowrap mb-0">
                      <thead>
                        <tr>
                          <th>#</th>
                          <th>Country</th>
                          <th>University </th>
                          <th>No of Courses </th>
                          <th>Exams Accepted </th>
                          <th>Fee Range</th>
                          <th>Our Agents </th>
                          <th>Actions </th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr>
                          <td>1</td>
                          <td>USA</td>
                          <td>Columbia University </td>
                          <td>120</td>
                          <td>IELTS,TOEFL</td>
                          <td>20L-50L</td>
                          <td>120</td>
                          <td>
                            <button
                              type="button"
                              className="btn btn-icon btn-sm"
                              title="View"
                            >
                              <i className="fa fa-eye"></i>
                            </button>
                            <button
                              type="button"
                              className="btn btn-icon btn-sm"
                              title="Edit"
                            >
                              <i className="fa fa-edit"></i>
                            </button>
                            <button
                              onClick={() => this.setState({ show: true })}
                              type="button"
                              className="btn btn-icon btn-sm js-sweetalert"
                              title="Delete"
                              data-type="confirm"
                            >
                              <i className="fa fa-trash-o text-danger"></i>
                            </button>
                          </td>
                        </tr>
                        <tr>
                          <td>2</td>
                          <td>USA</td>
                          <td>University of South Florida</td>
                          <td>180</td>
                          <td>TOEFL</td>
                          <td>10L-80L</td>
                          <td>152</td>
                          <td>
                            <button
                              type="button"
                              className="btn btn-icon btn-sm"
                              title="View"
                            >
                              <i className="fa fa-eye"></i>
                            </button>
                            <button
                              type="button"
                              className="btn btn-icon btn-sm"
                              title="Edit"
                            >
                              <i className="fa fa-edit"></i>
                            </button>
                            <button
                              type="button"
                              className="btn btn-icon btn-sm js-sweetalert"
                              title="Delete"
                              data-type="confirm"
                            >
                              <i className="fa fa-trash-o text-danger"></i>
                            </button>
                          </td>
                        </tr>
                        <tr>
                          <td>3</td>
                          <td>USA</td>
                          <td>New Jersey Institute of Technology</td>
                          <td>142</td>
                          <td>TOEFL</td>
                          <td>25L-80L</td>
                          <td>520</td>
                          <td>
                            <button
                              type="button"
                              className="btn btn-icon btn-sm"
                              title="View"
                            >
                              <i className="fa fa-eye"></i>
                            </button>
                            <button
                              type="button"
                              className="btn btn-icon btn-sm"
                              title="Edit"
                            >
                              <i className="fa fa-edit"></i>
                            </button>
                            <button
                              type="button"
                              className="btn btn-icon btn-sm js-sweetalert"
                              title="Delete"
                              data-type="confirm"
                            >
                              <i className="fa fa-trash-o text-danger"></i>
                            </button>
                          </td>
                        </tr>
                        <tr>
                          <td>4</td>
                          <td>USA</td>
                          <td>Columbia University</td>
                          <td>100</td>
                          <td>PET</td>
                          <td>25L-80L</td>
                          <td>98</td>
                          <td>
                            <button
                              type="button"
                              className="btn btn-icon btn-sm"
                              title="View"
                            >
                              <i className="fa fa-eye"></i>
                            </button>
                            <button
                              type="button"
                              className="btn btn-icon btn-sm"
                              title="Edit"
                            >
                              <i className="fa fa-edit"></i>
                            </button>
                            <button
                              type="button"
                              className="btn btn-icon btn-sm js-sweetalert"
                              title="Delete"
                              data-type="confirm"
                            >
                              <i className="fa fa-trash-o text-danger"></i>
                            </button>
                          </td>
                        </tr>
                        <tr>
                          <td>5</td>
                          <td>USA</td>
                          <td>University of North Texas.</td>
                          <td>48</td>
                          <td>GRE</td>
                          <td>10L-96L</td>
                          <td>231</td>
                          <td>
                            <button
                              type="button"
                              className="btn btn-icon btn-sm"
                              title="View"
                            >
                              <i className="fa fa-eye"></i>
                            </button>
                            <button
                              type="button"
                              className="btn btn-icon btn-sm"
                              title="Edit"
                            >
                              <i className="fa fa-edit"></i>
                            </button>
                            <button
                              type="button"
                              className="btn btn-icon btn-sm js-sweetalert"
                              title="Delete"
                              data-type="confirm"
                            >
                              <i className="fa fa-trash-o text-danger"></i>
                            </button>
                          </td>
                        </tr>
                      </tbody>
                    </table>
                    <SweetAlert
                      show={this.state.show}
                      title="Are you sure?"
                      text="You will not be able to recover this imaginary file!"
                      type="warning"
                      showCancelButton={true}
                      confirmButtonColor="#dc3545"
                      confirmButtonText="Yes, delete it!"
                      closeOnConfirm={false}
                      onConfirm={() => this.setState({ show: false })}
                    />
                  </div>
                </div>
              </TabPane>
              <TabPane tabId={2} className={classnames(["fade show"])}>
                <div className="col-lg-12 col-md-12">
                  <div
                    className={`card ${isCardRemove ? "card-remove" : ""} ${
                      isCollapsed ? "card-collapsed" : ""
                    }`}
                  >
                    <div className="card-header">
                      <h3 className="card-title">University info</h3>
                      <div className="card-options ">
                        <a
                          href
                          className="card-options-collapse"
                          onClick={() =>
                            this.setState({ isCollapsed: !isCollapsed })
                          }
                        >
                          <i className="fe fe-chevron-up"></i>
                        </a>
                        <a
                          href
                          className="card-options-remove"
                          onClick={() =>
                            this.setState({ isCardRemove: !isCardRemove })
                          }
                        >
                          <i className="fe fe-x"></i>
                        </a>
                      </div>
                    </div>
                    <div className="card-body">
                      <div className="row clearfix">
                        <div className="col-sm-4">
                          <div className="form-group">
                            <input
                              type="text"
                              className="form-control"
                              placeholder="Country Name "
                            />
                          </div>
                        </div>
                        <div className="col-sm-4">
                          <div className="form-group">
                            <input
                              type="text"
                              className="form-control"
                              placeholder="University Name"
                            />
                          </div>
                        </div>
                        <div className="col-sm-4">
                          <div className="form-group">
                            <input
                              type="text"
                              className="form-control"
                              placeholder="No of Courses"
                            />
                          </div>
                        </div>
                        <div className="col-sm-4">
                          <div className="form-group">
                            <input
                              type="number"
                              className="form-control"
                              placeholder="Exams Accepted  "
                            />
                          </div>
                        </div>
                        <div className="col-sm-4">
                          <div className="form-group">
                            <input
                              type="number"
                              className="form-control"
                              placeholder="Fee Range  "
                            />
                            {/* <DatePicker
                              placeholderText="Department Start Date"
                              className="form-control"
                            /> */}
                          </div>
                        </div>
                        <div className="col-sm-4">
                          <div className="form-group">
                            <input
                              type="number"
                              className="form-control"
                              placeholder="Our AGENTS"
                            />
                          </div>
                        </div>
                        <div className="col-sm-4">
                          <div className="form-group">
                            <input
                              type="text"
                              className="form-control"
                              placeholder="Intake"
                            />

                            {/* <textarea
                              rows="4"
                              className="form-control no-resize"
                              placeholder="Brief"
                            ></textarea> */}
                          </div>
                        </div>
                        <div className="col-sm-12">
                          <button
                            type="submit"
                            className="mr-1 btn btn-primary"
                          >
                            Submit
                          </button>
                          <button
                            type="submit"
                            className="btn btn-outline-secondary btn-default"
                          >
                            Cancel
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="card" style={{ display: "none" }}>
                    <div className="card-header">
                      <h3 className="card-title">Staff Member Account Info</h3>
                      <div className="card-options ">
                        <a
                          href
                          className="card-options-collapse"
                          data-toggle="card-collapse"
                        >
                          <i className="fe fe-chevron-up"></i>
                        </a>
                        <a
                          href
                          className="card-options-remove"
                          data-toggle="card-remove"
                        >
                          <i className="fe fe-x"></i>
                        </a>
                      </div>
                    </div>
                    <div className="card-body">
                      <div className="row clearfix">
                        <div className="col-sm-12">
                          <div className="form-group">
                            <input
                              type="text"
                              className="form-control"
                              placeholder="Email"
                            />
                          </div>
                        </div>
                        <div className="col-sm-12">
                          <div className="form-group">
                            <input
                              type="text"
                              className="form-control"
                              placeholder="Phone"
                            />
                          </div>
                        </div>
                        <div className="col-sm-12">
                          <button
                            type="submit"
                            className="mr-1 btn btn-primary"
                          >
                            Submit
                          </button>
                          <button
                            type="submit"
                            className="btn btn-outline-secondary btn-default"
                          >
                            Cancel
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </TabPane>
              <TabPane tabId={3} className={classnames(["fade show"])}>
                <div className="card">
                  <div className="card-header">
                    <h3 className="card-title">Add Agent</h3>
                    <div className="card-options ">
                      <a
                        href
                        className="card-options-collapse"
                        data-toggle="card-collapse"
                      >
                        <i className="fe fe-chevron-up"></i>
                      </a>
                      <a
                        href
                        className="card-options-remove"
                        data-toggle="card-remove"
                      >
                        <i className="fe fe-x"></i>
                      </a>
                    </div>
                  </div>
                  <form className="card-body">
                    <div className="form-group row">
                      <label className="col-md-3 col-form-label">
                        Agent Name <span className="text-danger">*</span>
                      </label>
                      <div className="col-md-7">
                        <input type="text" className="form-control" />
                      </div>
                    </div>
                    <div className="form-group row">
                      <label className="col-md-3 col-form-label">
                        University Name
                      </label>
                      <div className="col-md-7">
                        <input type="text" className="form-control" />
                      </div>
                    </div>
                    <div className="form-group row">
                      <label className="col-md-3 col-form-label">
                        Location <span className="text-danger">*</span>
                      </label>
                      <div className="col-md-7">
                        <DatePicker className="form-control" />
                      </div>
                    </div>
                    <div className="form-group row">
                      <label className="col-md-3 col-form-label">
                        Email <span className="text-danger">*</span>
                      </label>
                      <div className="col-md-7">
                        <input type="text" className="form-control" />
                      </div>
                    </div>
                    <div className="form-group row">
                      <label className="col-md-3 col-form-label">
                        Address <span className="text-danger">*</span>
                      </label>
                      <div className="col-md-7">
                        <textarea
                          rows="2"
                          className="form-control no-resize"
                          placeholder="Address"
                        ></textarea>
                      </div>
                    </div>
                    <div className="form-group row">
                      <label className="col-md-3 col-form-label"></label>
                      <div className="col-md-7">
                        <button type="submit" className="mr-1 btn btn-primary">
                          Submit
                        </button>
                        <button
                          type="submit"
                          className="btn btn-outline-secondary"
                        >
                          Cancel
                        </button>
                      </div>
                    </div>
                  </form>
                </div>
              </TabPane>
            </TabContent>
          </div>
        </div>
      </>
    );
  }
}
